::selection {
  background-color: rgba(255, 69, 0, 0.8);
}

.page {
  min-height: 100%;
  font-size: 10px;
  font-family: "Oswald Regular", "Arial", sans-serif;
  // line-height: 27px;
}

.body {
  min-height: 100%;
  margin: 0;
  padding: 0;
  background-color: $main-color;
  color: $black-color;
}

.hidden {
  display: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

@media (min-width: $tablet-width) {
  .mobile-visible {
    display: none;
  }
}

.mobile-only--hidden {
  display: none;

  @media (min-width: $mobile-width-only) {
    display: initial;
  }
}

@media (max-width: $mobile-width-only), (min-width: $desktop-width-only) {
  .tablet-visible {
    display: none;
  }
}

.desktop-visible {
  display: none;

  @media (min-width: $desktop-width) {
    display: initial;
  }
}

